export function formatNumberWithSpaceSeparator(number: number) {
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ' ');
};

export const getStopsLabel = (stops: number) => {
    switch (stops) {
        case 0:
            return " ";
        case 1:
            return "1 ";
        case 2:
            return "2 ";
        case 3:
            return "3 ";
        default:
            return `${stops} `;
    }
};